import React from 'react';
import { Switch, Route, Router } from 'react-router-dom';
import {
  StylesProvider,
  createGenerateClassName,
} from '@material-ui/core/styles';

import RolloutHistory from './components/RolloutHistory';
import OsuConfig from './components/OsuConfig';
import HashESN from './components/HashESN';
import IdentityOverrides from "./components/IdentityOverrides/IdentityOverrides";
import IdentityOverridesHistory from './components/IdentityOverrides/IdentityOverridesHistory';

  const generateClassName = createGenerateClassName({
  productionPrefix: 'dl',
});

export default ({ history }) => {
  return (
    <div>
      <StylesProvider generateClassName={generateClassName}>
        <Router history={history}>
          <Switch>
            <Route path="/" exact component={OsuConfig} />
            <Route path="/rollout-history" component={RolloutHistory} />
            <Route path="/hash-esn" component={HashESN} />
            <Route path="/target-devices" component={HashESN} />
            <Route path="/overrides" component={IdentityOverrides} />
            <Route path="/overrides-history" component={IdentityOverridesHistory} />
          </Switch>
        </Router>
      </StylesProvider>
    </div>
  );
};
