import React, { useEffect, useState } from 'react';
import Fuse from 'fuse.js';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import TableHead from '@material-ui/core/TableHead';
import Table from '@material-ui/core/Table';
import TableContainer from '@material-ui/core/TableContainer';
import TableBody from '@material-ui/core/TableBody';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Alert from '@material-ui/lab/Alert';
import { Flex, InputGroup, Row } from 'ff-core-components';

import Navbar from '../NavBar';
import { getOverridesHistory } from '../../services/overrides';
import CircularProgress from '@material-ui/core/CircularProgress';

const IdentityOverridesHistory = () => {
  const [lastEvaluatedKey, setLastEvaluatedKey] = useState(null); // eslint-disable-line
  const [loading, setLoading] = useState(true);
  const [searchInput, setSearchInput] = useState('');
  const [overridesHistory, setOverridesHistory] = useState([]);
  const [error, setError] = useState('');
  const [filteredOverridesHistory, setFilteredOverridesHistory] = useState([]);

  const fuse = new Fuse(overridesHistory, {
    keys: ['chipset_name', 'firmware_version', 'rollout_percentage', 'soc_name', 'updated_by'],
    findAllMatches: true,
    threshold: 0.3
  });

  useEffect(() => {
    getOverridesHistory()
      .then((res) => {
        setOverridesHistory(res.items);
        setFilteredOverridesHistory(res.items)
        setLastEvaluatedKey(res.lastEvaluatedKey);
      })
      .catch(err => setError(typeof err.response?.data === 'string' ? err.response?.data : err.message))
      .finally(() => setLoading(false));
  }, []);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setError('');
    }, 5000);

    return () => {
      clearTimeout(timeout);
    }
  }, [error]);

  useEffect(() => {
    if (searchInput === '') {
      setFilteredOverridesHistory(overridesHistory);
      return;
    }

    const result = fuse.search(searchInput);
    setFilteredOverridesHistory(result.map((r) => r.item));
  }, [searchInput]);

  const addHistory = () => {
    setLoading(true)
    getOverridesHistory(encodeURIComponent(JSON.stringify(lastEvaluatedKey)))
      .then((res) => {
        const newDataArray = [...overridesHistory, ...res.items];
        setOverridesHistory(newDataArray);
        setFilteredOverridesHistory(newDataArray);
        setLastEvaluatedKey(res.lastEvaluatedKey);
      })
      .catch(err => setError(typeof err.response?.data === 'string' ? err.response?.data : err.message))
      .finally(() => setLoading(false));
  }

  return (
    <div>
      <Navbar style={{ paddingLeft: '10px' }}/>

      <div className='container my-5 px-5 py-3' style={{background: 'white'}}>

      <h2 className="text-center">Identity Overrides History</h2>

      <Row>
        <Flex>
          <InputGroup
            inputProps={{
              className: 'full-width mb-2',
              name: 'overrides-history-search',
            }}
            title='Search for history'
            placeholder='Search for history'
            type='text'
            value={searchInput}
            onChange={(e) => {
              setSearchInput(e.target.value);
            }}
          />
        </Flex>
      </Row>

      {error.length > 0 && (
        <Row className="mb-1">
          <Flex>
            <Alert severity="error">{error}</Alert>
          </Flex>
        </Row>
      )}

      <TableContainer component={Paper}>
        <Table size="small" aria-label="identity-overrides-history-table">
          <TableHead>
            <TableRow>
              <TableCell align="left">Identifier</TableCell>
              <TableCell align="right">Updated_by</TableCell>
              <TableCell align="right">Action</TableCell>
              <TableCell align="right">Updated</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {filteredOverridesHistory.map((row, index) => (
              <TableRow key={index}>
                <TableCell component="th" scope="row">
                  {row.identifier}
                </TableCell>
                <TableCell align="right" style={{width: '200px'}}>{row.updated_by}</TableCell>
                <TableCell align="right" style={{width: '100px'}}>{row.action}</TableCell>
                <TableCell align="right" style={{width: '175px'}}>
                  {new Date(row.updated_on * 1000).toLocaleString(undefined, {
                    day: 'numeric',
                    month: 'numeric',
                    year: 'numeric',
                    hour: 'numeric',
                    minute: 'numeric',                                        
                  })}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {filteredOverridesHistory.length !== 0 && (
        <div className='flex justify-content-center mt-2'>
          <Button disabled={loading || lastEvaluatedKey === null} color="primary" onClick={() => addHistory()}>
            Show more
            {" "}
            {loading && <CircularProgress size={20} />}
          </Button>
        </div>
      )}
    </div>
  </div>
)
}

export default IdentityOverridesHistory;
