import { client } from './api';

const OVERRIDE_URL = '/osu/overrides';
const ROLLOUT_URL = '/osu/rollout'

export const getOverrides = async () => {
  return await client.get(OVERRIDE_URL).then((response) => {
    return response.data
  }).catch((error) => {
    throw error;
  })
}

export const createOverride = async (identifier) => {
  return await client.post(OVERRIDE_URL, identifier).then((response) => {
    return response
  }).catch((error) => {
    throw error;
  })
}

export const removeOverride = async (identifier) => {
  return await client.delete(`${OVERRIDE_URL}/${identifier}`).then((response) => {
    return response
  }).catch((error) => {
    throw error;
  })
}

export const getESNDetail = async (esn) => {
  return await client.get(`${ROLLOUT_URL}/check`, {
    params: {
      esn,
    }
  }).then((response) => {
    return response
  }).catch((error) => {
    throw error;
  })
}

export const getOverridesHistory = async (identifier = '') => {
  const params = {
    limit: 100
  };

  if (identifier) {
    params.lastEvaluatedKey = encodeURIComponent(identifier);
  }

  return await client.get(`${OVERRIDE_URL}/history`, {
    params
  }).then((response) => {
    return response.data
  }).catch((error) => {
    throw error;
  })
}
