import React, { useState, useEffect } from 'react';
import { Flex, Row, FormGroup, PanelSearch, AppLoader } from 'ff-core-components';
import useSearchThrottle from './useSearchThrottle';
import axios from 'axios';
import Navbar from './NavBar';
import moment from 'moment';
import './RolloutHistory.css';
import { makeStyles } from '@material-ui/core/styles';
import Skeleton from '@material-ui/lab/Skeleton';
import Grid from '@material-ui/core/Grid';
import AutoCloseAlert from './common/AutoCloseAlert';

function TypographyLoading(props) {
    return (
    <div>
      <Skeleton variant="text" height={80} />
      <Skeleton variant="rect" height={350} />
    </div>
    );
}

function SkeletonTypography(props) {
    return (
        <div style={{ width: '100%' }}>
            <Grid container >
                <Grid item md>
                <TypographyLoading loading={props.loading} />
                </Grid>
            </Grid>
        </div>
    );
}

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
}));

const widths = [200, 200, 200, 200, 200]
const RolloutHistory = ({ children }) => {
    const classes = useStyles();
    const [page, setPage] = useState(0)
    const [ loading, setLoading ] = useState(true);
    const [ historyData, setHistoryData ] = useState([]);
    const [ apiError, setApiError ] = useState(null);

    const fromParam = (str) => {
        const documentSearch =
        typeof document === 'undefined' ? '' : document.location.search

        if (!str && !documentSearch) {
        return {}
        }
        // eslint-disable-next-line
            const urlString = (str || documentSearch).replace(/(^\?)/, '');
        return JSON.parse(
        `{"${urlString.replace(/&/g, '","').replace(/=/g, '":"')}"}`,
        (key, value) => (key === '' ? value : decodeURIComponent(value)),
        )
    }
    const { search, searchInput, setSearchInput } = useSearchThrottle(
        fromParam().search,
        () => {
        setPage(1)
        },
    )

    const renderRow = ({
        updated_on,
        firmware_version,
        chipset_name,
        rollout_percentage,
        updated_by
      }) => {
        return (
          <Row className='list-item py-2 audit__item'>
            <span style={{ width: widths[0] }}>
                {
                    moment(updated_on * 1000).format('Do MMM YYYY HH:mma')
                }
              {/* {updated_on} */}
            </span>
            {/* <div style={{ width: widths[1] }}>
              {updated_by}
            </div> */}
            <Flex>{updated_by}</Flex>
            <span style={{ width: widths[2] }}>
              {firmware_version}
            </span>
            <div style={{ width: widths[3] }}>
              {chipset_name}
            </div>
            <div style={{ width: widths[4] }}>
              {rollout_percentage}
            </div>
            {/* <Flex>{rollout_percentage}</Flex> */}
          </Row>
        )
    }

    useEffect(() => {
        setLoading(true);
        const userData = JSON.parse(sessionStorage.getItem('userData'));
        const apiKey = userData.ephemeral_token;
        axios.get(`${process.env.API_URL}/osu/rollout/history`, {
        headers: {
            'x-api-key': apiKey
        }
        })
        .then(response => {
            if (response.data) {
                setHistoryData(response.data);
                setLoading(false);
            }
            setApiError(null);
        })
        .catch(error => {
            console.error('Error:', error);
            setApiError({ type: 'error', message: JSON.stringify(error) });
            setLoading(false);
        })
        .finally(() => {
            setLoading(false);
        })
    }, []);

    // if (loading) {
    //     return <AppLoader />
    // }

    return (
        <div>
            <Navbar style={{ paddingLeft: '10px' }}/>
            <div className='container my-5'>
                {apiError && (
                    <AutoCloseAlert
                        type={apiError.type}
                        message={apiError.message}
                        setApiError={setApiError}
                        className='my-3'
                        variant="filled"
                    />
                )}
                { loading ? <SkeletonTypography loading/> : (
                    <PanelSearch
                        id='rollout-history'
                        title='Rollout History'
                        isLoading={loading}
                        className='rollout-history no-pad'
                        items={historyData}
                        filter={search}
                        search={searchInput}
                        onChange={(e) => {
                        setSearchInput(e)
                        }}
                        nextPage={() => {
                            setPage(page + 1)
                        }}
                        prevPage={() => {
                            setPage(page - 1)
                        }}
                        goToPage={(page) => {
                            setPage(page)
                        }}
                        filterRow={(item, search) => {
                        const strToSearch = `${item.firmware_version} ${item.chipset_name} ${item.updated_by}`
                        return (
                            strToSearch.toLowerCase().indexOf(search.toLowerCase()) !== -1
                        )
                        }}
                        renderRow={renderRow}
                        header={
                        <Row className='table-header'>
                            <span style={{ width: widths[0] }}>Date</span>
                            <Flex>Updated By</Flex>
                            <div style={{ width: widths[2] }}>Firmware Version</div>
                            <div style={{ width: widths[3] }}>Chipset Name</div>
                            <div style={{ width: widths[4] }}>Rollout Percent</div>
                        </Row>
                        }
                        renderNoResults={
                        <FormGroup className='text-center'>
                            No rollout history available.
                        </FormGroup>
                        }
                    />
                )}
            </div>
        </div>
    )
};

export default RolloutHistory;
