// src/Button.js
import React from 'react';
import Navbar from './NavBar';
import { useEffect, useState } from 'react';
import { Button, Row, AppLoader, InputGroup, FormGroup } from 'ff-core-components';
import axios from 'axios';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import TableCell from '@material-ui/core/TableCell';
import MuiAlert from '@material-ui/lab/Alert';
import moment from 'moment';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Ribbon from './common/Ribbon';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 14,
    },
  }))(TableCell);

// const StyledTableRow = withStyles((theme) => ({
//     root: {
//         '&:nth-of-type(odd)': {
//         backgroundColor: theme.palette.action.hover,
//         },
//     },
// }))(TableRow);

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
    root: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
        position: 'relative',
        overflow: 'auto',
        maxHeight: 400,
    },
    listSection: {
        backgroundColor: 'inherit',
    },
    ul: {
        backgroundColor: 'inherit',
        padding: 0,
    },
}));

const HashESN = ({ children }) => {
    const classes = useStyles();
    const [ hashESN, setHashESN ] = useState('');
    const [ data, setData ] = useState(null);
    const [ loading, setLoading ] = useState(false);
    const [ error, setError ] = useState(null);

    const fetchData = async (esn) => {
        setLoading(true);
        setError(null);
        setData(null);
        try {
          const userData = JSON.parse(sessionStorage.getItem('userData'));
          const apiKey = userData.ephemeral_token;
          const response = await axios.get(`${process.env.API_URL}/osu/rollout/check`, {
            params: {
              esn,
            },
            headers: {
                'x-api-key': apiKey
            }
          });
          setData(response.data);
        } catch (error) {
            if (error.response) {
                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx
                setError(error.response.data);
              } else if (error.request) {
                // The request was made but no response was received
                setError({ error: 'No response received' });
              } else {
                // Something happened in setting up the request that triggered an error
                setError({ error: 'Request failed' });
              }
        } finally {
          setLoading(false);
        }
    };
    return (
        <div>
            <Navbar style={{ paddingLeft: '10px' }}/>
            <div className='container my-5'>
                <div className='panel panel--flat panel-without-heading'>
                    <div className='panel-content'>
                        <FormGroup className='mb-4 mr-3 ml-3'>
                            <InputGroup
                                inputProps={{
                                className: 'full-width mb-2',
                                name: 'hashESN',
                                }}
                                title='Device ESN:'
                                placeholder='e.g. LTABCDEF1234567'
                                type='text'
                                value={hashESN}
                                onChange={(e) =>
                                    setHashESN(e.target.value.trim())
                                }
                            />
                            <div className="text-center">
                                <Button disabled={!hashESN || hashESN.trim() === ''} onClick={() => fetchData(hashESN)}>Check Info</Button>
                            </div>
                        </FormGroup>
                        {
                            data && (
                                <List className={classes.root}>
                                    {
                                        !error ?
                                        (!data.is_canary ?
                                            <Ribbon text="Canary InActive" color="error"/> :
                                            <Ribbon text="Canary Active" color="success"/>)
                                        : null
                                    }
                                    <li key={`section`} className={classes.listSection}>
                                    <ul className={classes.ul}>
                                        {['Firmware Version', 'SOC Name', 'Chipset Name', 'Model', 'Percentage', 'Rollout Percentage', 'Updated On'].map((item, index) => (
                                        <>
                                        {
                                            item === 'Firmware Version' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.firmwareVersion}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'SOC Name' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.soc_name}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'Chipset Name' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.chipset_name}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'Model' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.model}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'Percentage' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.percentage}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'Rollout Percentage' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{data.rollout_percentage}</span></span>} />
                                            </ListItem>
                                        }
                                        {
                                            item === 'Updated On' && <ListItem key={`item-${index}`}>
                                                <ListItemText primary={<span><b>{item}&nbsp;:&nbsp;&nbsp;</b><span>{moment(data.updated_on * 1000).format('Do MMM YYYY HH:mma')}</span></span>} />
                                            </ListItem>
                                        }

                                        </>
                                        ))}
                                    </ul>
                                    </li>
                                </List>
                            )
                        }
                        {/* {
                            data && (
                                <div style={{ padding: '1rem 0.75rem' }}>
                                    <TableContainer component={Paper}>
                                        <Table className={classes.table} aria-label="customized table">
                                            <TableHead>
                                            <TableRow>
                                                <StyledTableCell>Firmware Version</StyledTableCell>
                                                <StyledTableCell>SOC Name</StyledTableCell>
                                                <StyledTableCell>Chipset Name</StyledTableCell>
                                                <StyledTableCell>Model</StyledTableCell>
                                                <StyledTableCell align="right">Percentage</StyledTableCell>
                                                <StyledTableCell align="right">Rollout Percentage</StyledTableCell>
                                                <StyledTableCell align="right">Updated On</StyledTableCell>
                                            </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                <StyledTableRow key={data.firmwareVersion}>
                                                <StyledTableCell component="th" scope="row">
                                                    {data.firmwareVersion}
                                                </StyledTableCell>
                                                <StyledTableCell>{data.soc_name}</StyledTableCell>
                                                <StyledTableCell>{data.chipset_name}</StyledTableCell>
                                                <StyledTableCell>{data.model}</StyledTableCell>
                                                <StyledTableCell align="right">{data.percentage}</StyledTableCell>
                                                <StyledTableCell align="right">{data.rollout_percentage}</StyledTableCell>
                                                <StyledTableCell align="right">{moment(data.updated_on * 1000).format('Do MMM YYYY HH:mma')}</StyledTableCell>
                                                </StyledTableRow>
                                            </TableBody>
                                        </Table>
                                    </TableContainer>
                                </div>
                            )
                        } */}
                        {
                            error && (
                                <div style={{ padding: '1rem 0.75rem' }}>
                                    <Alert severity="error">{error.error}</Alert>
                                </div>
                            )
                        }
                    </div>
                </div>
                {
                    loading && <div>
                        <Backdrop className={classes.backdrop} open={true}>
                            <CircularProgress />
                        </Backdrop>
                    </div>
                }
            </div>
        </div>
    );
};

export default HashESN;
