import { useEffect, useState } from 'react'
import useThrottle from './useThrottle'

export default function useSearchThrottle(
  defaultValue,
  cb,
  minSearchLength
) {
  const [search, setSearch] = useState(defaultValue || '')
  const [searchInput, setSearchInput] = useState(search)
  const searchItems = useThrottle((search) => {
    setSearch(search)
    cb?.()
  }, 100)
  useEffect(() => {
    if (minSearchLength) {
      if ((searchInput.length > minSearchLength) || searchInput.length === 0){
        searchItems(searchInput)
      }
    } else {
      searchItems(searchInput)
    }
    //eslint-disable-next-line
    }, [searchInput]);
  return { search, searchInput, setSearchInput }
}
/* Usage example:
const searchItems =  useThrottle((search:string) => {
  doThing()
}, 100)
*/
