import React from "react";
import AlertDialogIdentity from "../common/AlertDialogIdentity";

const IdentityOverrideRemoveModal = ({ loading, isOpen, identifier, onClose, onSubmit }) => {
  return (
    <AlertDialogIdentity
      title="Remove Identity Override"
      text={<span>Are you sure you want to remove the <strong>{identifier}</strong> identity override?</span>}
      isOpen={isOpen}
      loading={loading}
      handleClose={onClose}
      handleSubmit={() => onSubmit(identifier)}
    />
  )
}

export default IdentityOverrideRemoveModal
